<template>
  <div class="container">
    <page-title first-title="线上选品"></page-title>
    <div class="search-wrapper">
      <el-tabs type="card">
        <el-tab-pane v-for="item in retailState" :label="item.label" :key="item.id">
          <span slot="label"
            >{{ item.label }}
            <span v-show="item.id == '2'">( {{ checklist.length }} )</span>
          </span>
          <el-form :inline="true" v-show="item.id == '1'" class="demo-form-inline" :model="params">
            <el-form-item label="商品编号">
              <el-input v-model="params.goods_id" placeholder="商品编号" clearable></el-input>
            </el-form-item>
            <el-form-item label="报名主题">
              <el-select v-model="params.signup_id" filterable clearable placeholder="请选择" style="display:block">
                <el-option v-for="item in options1" :key="item.id" :label="item.secName" :value="item.id"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="商品分类">
              <el-select v-model="params.category_id" filterable clearable placeholder="请选择" style="display:block">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="所属店铺">
              <el-select v-model="params.suppliers_id" filterable clearable placeholder="请选择" style="display:block">
                <el-option
                  v-for="item in options2"
                  :key="item.suppliersId"
                  :label="item.suppliersName"
                  :value="item.suppliersId"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
              <el-button type="primary" @click="clear" icon="el-icon-refresh-left">清空</el-button>
            </el-form-item>
          </el-form>

          <div class="list-wrapper">
            <p class="red">填写秒杀库存之后的商品可选择，每人限购值为“0”代表不限购</p>
            <el-table
              :data="item.id == '1' ? list : checklist"
              :ref="item.id == '1' ? 'tableData' : ''"
              tooltip-effect="dark"
              :header-cell-style="{ background: '#eef1f6' }"
              style="width: 100%"
              :row-key="getRowKeys"
              @select="checked(...arguments, item.id)"
              @select-all="checkAll(...arguments, item.id)"
              @selection-change="handleSelectionChange"
            >
              <el-table-column
                type="selection"
                width="30"
                :selectable="checkboxT"
                :reserve-selection="true"
              ></el-table-column>
              <el-table-column prop="goods_id" label="商品编号"></el-table-column>
              <el-table-column prop="goods_name" label="商品名称"></el-table-column>
              <el-table-column prop="goods_number" label="可售库存"></el-table-column>
              <el-table-column prop="surplus_quantity" label="秒杀可售库存" sortable></el-table-column>
              <el-table-column label="秒杀库存">
                <template slot-scope="scope">
                  <input
                    @blur="changeNum(scope.row, 0)"
                    v-model.number="scope.row.secbuy_quantity"
                    type="number"
                    class="nums"
                  />
                  <p class="red">{{ scope.row.number1 }}</p>
                </template>
              </el-table-column>
              <el-table-column label="每人限购">
                <template slot-scope="scope">
                  <input
                    @blur="changeNum(scope.row, 2)"
                    v-model.number="scope.row.max_quantity"
                    type="number"
                    class="nums"
                  />
                  <p class="red">{{ scope.row.number1 }}</p>
                </template>
              </el-table-column>
              <el-table-column label="排序值">
                <template slot-scope="scope">
                  <input
                    v-model.number="scope.row.goods_order"
                    type="number"
                    :min="1"
                    :max="100"
                    @blur="changeNum(scope.row.goods_order, 1)"
                    class="nums"
                  />
                  <p class="red">{{ scope.row.number2 }}</p>
                </template>
              </el-table-column>
              <el-table-column label="原价">
                <template slot-scope="scope">
                  <p>{{ utils.formatMoney(scope.row.goods_shop_price) }}</p>
                  <p>
                    {{
                      `(现金价:${utils.formatMoney(scope.row.goods_cash)} + 积分支付上限:${utils.formatMoney(
                        scope.row.goods_money_line
                      )})`
                    }}
                  </p>
                </template>
              </el-table-column>
              <el-table-column prop="shop_price" label="秒杀总价" sortable></el-table-column>
              <el-table-column prop="cash_price" label="秒杀现金价"></el-table-column>
              <el-table-column prop="huanbi_price" label="秒杀积分上限"></el-table-column>
              <el-table-column prop="shop_name" label="所属店铺"></el-table-column>
              <el-table-column prop="signup_discount" label="秒杀折扣" :formatter="forscale" sortable></el-table-column>
              <el-table-column
                prop="signup_proportion"
                label="积分比例"
                :formatter="forscale"
                sortable
              ></el-table-column>
              <el-table-column prop="signup_frequency" label="参与秒杀次数" sortable></el-table-column>
              <el-table-column prop="signup_sales" label="秒杀累计销量" sortable></el-table-column>
              <el-table-column prop="project_sales" label="商品销量" sortable></el-table-column>
            </el-table>
          </div>

          <div v-show="item.id != '1'" style="margin-top:20px;">
            <el-button type="primary" @click="deleteItem" size="medium">删除</el-button>
          </div>
          <div v-show="item.id == '1'">
            <el-pagination
              background
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-size="params.limit"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
            <div class="btn-box">
              <el-button type="primary" @click="submitcheck" size="medium">提交</el-button>
              <el-button type="primary" @click="canlecheck" size="medium">取消</el-button>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RetailSupplierList',
  data() {
    return {
      // 获取row的key值
      getRowKeys(row) {
        return row.goods_id
      },
      list: [],
      options: [],
      options1: [],
      options2: [],
      checklist: [],
      currentPage: 1,
      retailState: [{ label: '全部', id: '1' }, { label: '已选中', id: '2' }],
      params: {
        page: 1,
        limit: 6,
        signup_id: '',
        goods_id: '',
        category_id: '',
        suppliers_id: '',
        secbuy_id: this.$route.query.id ? this.$route.query.id : ''
      },
      total: 0,
      multipleSelection: [],
      checkable: true,
      deleteArr: [], //已选中删除数组
      hodeArr: [] // 选中暂存
    }
  },
  created() {
    // this.getList()
    this.gettypeList()
  },
  methods: {
    changeNum(val, n) {
      //限制长度
      if (n == 0) {
        // console.log(val.secbuy_quantity)
        // console.log(val.surplus_quantity)
        if (val.secbuy_quantity > val.surplus_quantity) {
          this.$message.error('秒杀库存不能大于秒杀可售库存')
        } else if (val.secbuy_quantity < 1) {
          this.$message.error('秒杀库存不能小于1')
        }
      } else if (n == 1) {
        if (val > 100 || val < 1) {
          this.$message.error('排序值取值范围为1-100')
          val = 100
        }
        if (val.length > 3) {
          val = val.splice(0, 3)
        }
      }
    },
    handleSelectionChange(rows) {
      this.multipleSelection = rows
      this.select_order_number = this.multipleSelection.length
      this.select_orderId = []
      if (rows) {
        rows.forEach(row => {
          if (row) {
            this.select_orderId.push(row.orderid)
          }
        })
      }
    },
    // 获取商品一级类目
    gettypeList() {
      this.$api.get('hd/itzSecbuyGoods/selection', null, r => {
        // console.log(r)
        if (r.status == 200) {
          this.options = r.data.category
          this.options1 = r.data.signupNames
          this.options2 = r.data.suppliers
        }
      })
    },
    search() {
      this.params.page = 1
      if (this.params.signup_id) {
        this.getList()
      } else {
        this.$message.error('请选择报名主题')
      }
    },
    getList() {
      this.$api.get('hd/itzSecbuyGoods', this.params, r => {
        if (r.status == 200) {
          if (r.data.total > 0) {
            this.list = r.data.rows
            // this.list.forEach(item => {
            //   if (item.max_quantity == 0) {
            //     item.max_quantity = ''
            //   }
            // })
            this.total = r.data.total
            if (this.checklist.length > 0) {
              this.checklist.forEach(row => {
                this.list.forEach((item, index) => {
                  if (item.goods_id == row.goods_id) {
                    this.list[index] = row
                  }
                })
              })
            }
          } else {
            this.$confirm('该报名主题下暂时没有候选商品', '提示', {
              confirmButtonText: '确定',
              type: 'warning'
            })
          }
        } else {
          this.list = {}
          this.$message.error(r.msg)
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getList()
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return date ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3) : date
    },
    forscale: function(row, column) {
      let date = row[column.property]
      return date ? date + '%' : ''
    },
    clear() {
      this.params.page = 1
      this.params.limit = 6
      this.params.signup_id = ''
      this.params.goods_id = ''
      this.params.category_id = ''
      this.params.suppliers_id = ''
    },
    pricehistory(id) {
      let q = this.list.findIndex(value => value.goodsId == id) //根据sku货号找到对应的数据条目
      // console.log(q)
      // console.log(this.list)
      // console.log(this.list[q])
      localStorage.setItem('msg', JSON.stringify(this.list[q]))
      this.$router.push({ name: 'PriceHistory', params: { id: id } })
    },
    checkboxT(row) {
      return row.secbuy_quantity ? true : false
    },
    // 全选列表里面出发选中
    checked(selection, row, id) {
      // id='1'为全部，2，为已选中
      if (id == '1') {
        // 如果selected为true表示勾选，为false或者0表示取消勾选
        var selected = selection.length && selection.indexOf(row) !== -1
        // 锁定
        if (selected) {
          // console.log('锁定')
          this.$api.put(
            `hd/itzSecbuySignupGoods/goods/lock/${row.signup_id}`,
            null,
            res => {},
            err => {
              console.log('失败')
              this.$confirm(`${err.message}`, '提示', {
                confirmButtonText: '确定'
              })
              this.$refs.tableData[0].toggleRowSelection(row)
            }
          )
        } else {
          // console.log('解锁')
          this.$api.put(
            `hd/itzSecbuySignupGoods/goods/unlock/${row.signup_id}`,
            null,
            res => {},
            err => {
              this.$confirm(`${err.message}`, '提示', {
                confirmButtonText: '确定'
              })
              this.$refs.tableData[0].toggleRowSelection(row)
            }
          )
        }
        this.checklist = selection
      } else {
        this.deleteArr = selection
      }
    },
    checkAll(val, id) {
      if (id == '1') {
        var lockArr = []

        // console.log(lockArr)
        // console.log(val)
        // 如果val不为空为true表示勾选，为false或者0表示取消勾选
        if (val.length > 0) {
          val.forEach(item => {
            lockArr.push(item.signup_id)
          })
          // 全选
          this.$api.put(`hd/itzSecbuySignupGoods/goods/lock/`, lockArr, res => {
            if (res.status == 200) {
              if (res.data && res.data.length > 0) {
                this.$confirm(`${res.message}`, '提示', {
                  confirmButtonText: '确定'
                })
                var find = [],
                  vval = {}
                res.data.forEach(item => {
                  vval = this.list.find(val => {
                    return val.signup_id == item
                  })
                  // console.log(item, vval)
                  find.push(vval)
                })
                find.forEach(row => {
                  // console.log(row)
                  this.$refs.tableData[0].toggleRowSelection(row)
                })
              }
            }
          })
        } else {
          this.list.forEach(item => {
            if (item.secbuy_quantity) {
              lockArr.push(item.signup_id)
            }
          })
          // 取消全选
          this.$api.put(`hd/itzSecbuySignupGoods/goods/unlock/`, lockArr, r => {})
        }
        this.checklist = val
      } else {
        this.deleteArr = val
      }
    },
    canlecheck() {
      localStorage.clear()
      this.$router.go(-1)
    },
    submitcheck() {
      // 提交到新建秒杀活动列表
      if (this.checklist.length > 0) {
        this.checklist.forEach(val => {
          // 添加默认排序值
          return val.goods_order ? val.goods_order : (val.goods_order = 100)
        })
        // console.log(this.checklist)
        localStorage.setItem('productlist', JSON.stringify(this.checklist))
        // this.$confirm(`已成功提交${this.checklist.length}款商品`, '提示', {
        //   confirmButtonText: '确定'
        // })
        let id = this.$route.query.id,
          status = this.$route.query.status
        this.$router.replace({ name: 'SecondKillAdd', query: { operate: 'productlist', id: id, status: status } })
      } else {
        this.$confirm('请选择商品后再提交', '提示', {
          confirmButtonText: '确定',
          type: 'warning'
        })
      }
    },
    deleteItem() {
      // 批量删除已选中
      var arr = this.checklist
      var brr = this.deleteArr
      var idArr = []
      // console.log(arr,brr)
      for (var i = 0; i < brr.length; i++) {
        idArr.push(brr[i].signup_id)
        for (var j = 0; j < arr.length; j++) {
          if (arr[j] === brr[i]) {
            arr.splice(j, 1)
            j--
          }
        }
      }
      this.checklist = arr
      this.$api.put(`hd/itzSecbuySignupGoods/goods/unlock/`, idArr, r => {})
      // brr.forEach(row => {
      //   this.$refs.a[0].toggleRowSelection(row);
      // })
    }
  }
}
</script>

<style lang="scss" scoped>
.button-wrapper {
  text-align: right;
  padding: 20px 20px 10px;
}
.container {
  // min-width: 1366px;
  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 10px;
    }
  }
  .list-wrapper {
    margin-top: 10px;
    .nums {
      width: 60px;
      border-radius: 2px;
      border: 1px solid #ddd;
      height: 20px;
      text-indent: 4px;
    }
    /deep/.el-table .cell {
      text-overflow: clip;
    }
  }
}
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
.retails {
  .el-dialog__header {
    background: #505763;
    span {
      color: #fff;
    }
  }
}
.el-button--text {
  width: 100%;
  display: block;
  margin: 0;
  text-align: center;
  padding: 4px 0;
}
.el-table td,
.el-table th {
  padding: 6px 0;
  .red {
    font-size: 10px;
    line-height: 12px;
    margin: 4px 0;
  }
}
.red {
  color: #f00;
}
.btn-box {
  text-align: center;
  padding: 20px;
}
</style>
